<template>
  <div class="auth-container">
    <div class="auth-box" :class="{ 'slide-up': !isLogin }">
      <transition name="fade" mode="out-in">
        <!-- Î°úÍ∑∏Ïù∏ Ìèº -->
        <form v-if="isLogin" @submit.prevent="handleLogin" key="login">
          <h1>Î°úÍ∑∏Ïù∏</h1>
          <div class="input-group">
            <input 
              type="email" 
              v-model="loginForm.email" 
              placeholder="Ïù¥Î©îÏùº"
              @blur="validateEmail(loginForm.email)"
              required
            >
          </div>
          <div class="input-group">
            <input 
              type="password" 
              v-model="loginForm.password" 
              placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏"
              required
            >
          </div>
          <div class="checkbox-group">
            <label>
              <input type="checkbox" v-model="loginForm.rememberMe">
              <span>Remember me</span>
            </label>
          </div>
          <button type="submit">Î°úÍ∑∏Ïù∏</button>
          <p class="switch-form">
            Í≥ÑÏ†ïÏù¥ ÏóÜÏúºÏã†Í∞ÄÏöî? 
            <span @click="toggleForm">ÌöåÏõêÍ∞ÄÏûÖ</span>
          </p>
        </form>

        <!-- ÌöåÏõêÍ∞ÄÏûÖ Ìèº -->
        <form v-else @submit.prevent="handleRegister" key="register">
          <h1>ÌöåÏõêÍ∞ÄÏûÖ</h1>
          <div class="input-group">
            <input 
              type="text" 
              v-model="registerForm.nickname" 
              placeholder="ÎãâÎÑ§ÏûÑ"
              required
            >
          </div>
          <div class="input-group">
            <input 
              type="email" 
              v-model="registerForm.email" 
              placeholder="Ïù¥Î©îÏùº"
              @blur="validateEmail(registerForm.email)"
              required
            >
          </div>
          <div class="input-group">
            <input 
              type="password" 
              v-model="registerForm.password" 
              placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏"
              required
            >
          </div>
          <div class="input-group">
            <input 
              type="password" 
              v-model="registerForm.confirmPassword" 
              placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏"
              required
            >
          </div>
          <div class="checkbox-group">
            <label>
              <input type="checkbox" v-model="registerForm.termsAgreed">
              <span>ÏïΩÍ¥ÄÏóê ÎèôÏùòÌï©ÎãàÎã§</span>
            </label>
          </div>
          <button type="submit">ÌöåÏõêÍ∞ÄÏûÖ</button>
          <p class="switch-form">
            Ïù¥ÎØ∏ Í≥ÑÏ†ïÏù¥ ÏûàÏúºÏã†Í∞ÄÏöî? 
            <span @click="toggleForm">Î°úÍ∑∏Ïù∏</span>
          </p>
        </form>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SignInPage',
  data() {
    return {
      isLogin: true,
      loginForm: {
        email: '',
        password: '',
        rememberMe: false
      },
      registerForm: {
        nickname: '',
        email: '',
        password: '',
        confirmPassword: '',
        termsAgreed: false
      }
    }
  },
  methods: {
    validateEmail(email) {
      const regex = /^[\w-]+@([\w-]+\.)+[\w-]{2,4}$/
      if (!regex.test(email)) {
        alert('ÏûòÎ™ªÎêú Ïù¥Î©îÏùº ÌòïÏãùÏûÖÎãàÎã§.')
        return false
      }
      return true
    },
    handleLogin() {
      const storedEmail = localStorage.getItem('userEmail')
      const storedPassword = localStorage.getItem('userPassword')

      if (this.loginForm.email === storedEmail && this.loginForm.password === storedPassword) {
        localStorage.setItem('isLoggedIn', 'true')
        if (this.loginForm.rememberMe) {
          localStorage.setItem('rememberMe', 'true')
        }
        alert('Î°úÍ∑∏Ïù∏ ÏÑ±Í≥µ!')
        this.$router.push('/home')
      } else {
        alert('Ïù¥Î©îÏùº ÎòêÎäî ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§.')
      }
    },
    handleRegister() {
      if (!this.validateEmail(this.registerForm.email)) return
      
      if (this.registerForm.password !== this.registerForm.confirmPassword) {
        alert('ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§.')
        return
      }

      if (!this.registerForm.termsAgreed) {
        alert('ÏïΩÍ¥ÄÏóê ÎèôÏùòÌï¥Ï£ºÏÑ∏Ïöî.')
        return
      }
      //ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Ï†ÄÏû•
      localStorage.setItem('userEmail', this.registerForm.email)
      localStorage.setItem('userPassword', this.registerForm.password)
      localStorage.setItem('userNickname', this.registerForm.nickname)
      
      alert('ÌöåÏõêÍ∞ÄÏûÖ ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§.')

      //Î°úÍ∑∏Ïù∏ ÌèºÏúºÎ°ú Ï†ÑÌôòÌïòÍ≥† Ïù¥Î©îÏùº ÏûêÎèôÏûÖÎ†•
      this.isLogin = true
      this.loginForm.email = this.registerForm.email
    },
    toggleForm() {
      this.isLogin = !this.isLogin
    }
  }
}
</script>

<style scoped>
