  background-color: #c04040;
}

.switch-form {
  margin-top: 20px;
  color: #ffffff;
  text-align: center;
  font-size: 14px;
}

.switch-form span {
  color: #ffffff;
  cursor: pointer;
}

.switch-form span:hover {
  color: #ff0000;
}

/* Î∞ïÏä§ Ïä¨ÎùºÏù¥Îìú Ìö®Í≥º */
.auth-box {
  transition: transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.auth-box.slide-up {
  transform: translateY(-20px);
}

/* ÏûÖÎ†• ÌïÑÎìú Ìè¨Ïª§Ïä§ Ìö®Í≥º */
input:focus {
