import axios from 'axios';

const instance = axios.create({
    baseURL: 'https://api.themoviedb.org/3',
    params: {
        api_key: process.env.VUE_APP_TMDB_API_KEY,
        language: 'ko-KR'
    },
    headers: {
        'Content-Type': 'application/json'
    }
});

// ÏöîÏ≤≠ Ïù∏ÌÑ∞ÏÖâÌÑ∞ Ï∂îÍ∞Ä
instance.interceptors.request.use(
    config => {
        console.log('API ÏöîÏ≤≠ ÏÑ§Ï†ï:', {
            url: config.url,
            method: config.method,
            params: config.params
        });
        return config;
    },
    error => {
        console.error('API ÏöîÏ≤≠ ÏóêÎü¨:', error);
        return Promise.reject(error);
    }
);

// ÏùëÎãµ Ïù∏ÌÑ∞ÏÖâÌÑ∞ Ï∂îÍ∞Ä
instance.interceptors.response.use(
    response => {
        console.log('API ÏùëÎãµ ÏÑ±Í≥µ:', response.status);
        return response;
    },
    error => {
        console.error('API ÏùëÎãµ ÏóêÎü¨:', error.response?.status, error.message);
        return Promise.reject(error);
    }
);

