<template>
  <div class="search-page">
    <!-- ÌïÑÌÑ∞ÎßÅ ÏÑπÏÖò -->
    <div class="filters">
      <div class="search-input-container">
        <input
          v-model="searchQuery"
          type="text"
          placeholder="ÏòÅÌôî Ï†úÎ™© Í≤ÄÏÉâ"
          @input="handleSearch"
        
          @focus="showRecentSearches = true"
          @blur="handleBlur"
        />
        <div v-if="showRecentSearches && recentSearches.length > 0" class="recent-searches-dropdown">
          <div v-for="(search, index) in recentSearches" 
              :key="index" 
              class="recent-search-item">
            <span @click="applyRecentSearch(search)">{{ search }}</span>
            <button @click.stop="removeRecentSearch(index)" class="remove-btn">√ó</button>
          </div>
        </div>
      </div>
    
      <select v-model="selectedGenre" @change="applyFilters">
        <option value="">Ïû•Î•¥ ÏÑ†ÌÉù</option>
        <option value="28">Ïï°ÏÖò</option>
        <option value="12">Î™®Ìóò</option>
        <option value="16">Ïï†ÎãàÎ©îÏù¥ÏÖò</option>
        <option value="35">ÏΩîÎØ∏Îîî</option>
        <option value="27">Í≥µÌè¨</option>
        <option value="9648">ÎØ∏Ïä§ÌÑ∞Î¶¨</option>
        <option value="18">ÎìúÎùºÎßà</option>
        <option value="53">Ïä§Î¶¥Îü¨</option>
        <option value="14">ÌåêÌÉÄÏßÄ</option>
      </select>

      <select v-model="minRating" @change="applyFilters">
        <option value="">ÌèâÏ†ê ÏÑ†ÌÉù</option>
        <option value="8">8Ï†ê Ïù¥ÏÉÅ</option>
        <option value="7">7Ï†ê Ïù¥ÏÉÅ</option>
        <option value="6">6Ï†ê Ïù¥ÏÉÅ</option>
      </select>

      <select v-model="sortBy" @change="applyFilters">
        <option value="">Ï†ïÎ†¨ Í∏∞Ï§Ä</option>
        <option value="popularity">Ïù∏Í∏∞Ïàú</option>
        <option value="release_date">Í∞úÎ¥âÎÖÑÎèÑÏàú</option>
        <option value="vote_average">ÌèâÏ†êÏàú</option>
      </select>

      <button @click="resetFilters" class="reset-button">ÌïÑÌÑ∞ Ï¥àÍ∏∞Ìôî</button>
    </div>
    <!-- Ïπ¥ÌÖåÍ≥†Î¶¨Î≥Ñ ÏÑπÏÖò Ï∂îÍ∞Ä -->
    <div class="category-section">
      <div class="scroll-container">
        <div
          v-for="movie in popularMovies"
          :key="movie.id"
          class="movie-card"
          @click="goToDetail(movie.id)"
        >
          <img
            :src="`https://image.tmdb.org/t/p/w500${movie.poster_path}`"
            :alt="movie.title"
          />
          <div class="movie-info">
            <h3>{{ movie.title }}</h3>
            <div class="rating">‚≠ê {{ movie.vote_average.toFixed(1) }}</div>
          </div>
        </div>
      </div>
    </div>

    <div class="category-section">
      <h2>Îã§ÏãúÎ≥¥Í∏∞ Ï∂îÏ≤ú ÏΩòÌÖêÏ∏†</h2>
      <div class="scroll-container">
        <div
          v-for="movie in recommendedMovies"
          :key="movie.id"
          class="movie-card"
          @click="goToDetail(movie.id)"
        >
          <img
            :src="`https://image.tmdb.org/t/p/w500${movie.poster_path}`"
            :alt="movie.title"
          />
          <div class="movie-info">
            <h3>{{ movie.title }}</h3>
            <div class="rating">‚≠ê {{ movie.vote_average.toFixed(1) }}</div>
          </div>
        </div>
      </div>
    </div>

    <!-- Í≤∞Í≥º ÏÑπÏÖò -->
    <div class="movies-grid" v-if="!loading">
      <div
        v-for="movie in filteredMovies"
        :key="movie.id"
        class="movie-card"
        @click="goToDetail(movie.id)"
      >
        <img
          :src="`https://image.tmdb.org/t/p/w500${movie.poster_path}`"
          :alt="movie.title"
        />
        <div class="movie-info">
          <h3>{{ movie.title }}</h3>
          <div class="rating">‚≠ê {{ movie.vote_average.toFixed(1) }}</div>
        </div>
      </div>
    </div>
    <div v-else class="loading">Loading...</div>
  </div>
</template>

<script>
import { tmdbApi } from '@/api/tmdb';
import { useMovieSearch } from '../composables/useMovieSearch'
import MovieList from '../components/MovieList.vue'

export default {
  name: 'SearchPage',
  data() {
    return {
      movies: [],
      filteredMovies: [],
      recentSearches: [],
      showRecentSearches: false,
      maxRecentSearches: 5,
      loading: true,
      searchQuery: '',
      selectedGenre: '',
      minRating: '',
      sortBy: '',
    };
  },
  methods: {
    components: {
    MovieList
    },
    setup() {
      const {
        searchQuery,
        searchResults,
        isLoading,
        error,
        hasResults,
        searchMovies,
        clearSearch
      } = useMovieSearch()
      return {
        searchQuery,
        searchResults,
        isLoading,
        error,
        hasResults,
        searchMovies,
        clearSearch
      }
    },
    async fetchMovies() {
      try {
        const response = await tmdbApi.getPopular();
        this.movies = response.data.results;
        this.filteredMovies = [...this.movies];
        this.loading = false;
      } catch (error) {
        console.error('Error:', error);
      }
    },
    handleSearch() {
      if (this.searchQuery.trim()) {
      // Í≤ÄÏÉâÏñ¥Í∞Ä ÏûàÏùÑ ÎïåÎßå ÏµúÍ∑º Í≤ÄÏÉâÏñ¥Ïóê Ï†ÄÏû•
        this.saveRecentSearch(this.searchQuery.trim());
    
      // Í∏∞Ï°¥ Í≤ÄÏÉâ Î°úÏßÅ
        this.filteredMovies = this.movies.filter((movie) => movie.title.toLowerCase().includes(this.searchQuery.toLowerCase()));
      } else {
        this.filteredMovies = [...this.movies];
      }
      this.applyFilters();
    },
    // ÏµúÍ∑º Í≤ÄÏÉâÏñ¥ Ï†ÄÏû• Î©îÏÜåÎìú Ï∂îÍ∞Ä
    saveRecentSearch(query) {
      if (!query) return;
      
      let searches = [...this.recentSearches];
      // Ï§ëÎ≥µ Í≤ÄÏÉâÏñ¥ Ï†úÍ±∞
      const index = searches.indexOf(query);
      if (index > -1) {
        searches.splice(index, 1);
      }
      // ÏÉà Í≤ÄÏÉâÏñ¥ Ï∂îÍ∞Ä
      searches.unshift(query);
      // ÏµúÎåÄ 5Í∞úÎ°ú Ï†úÌïú
      searches = searches.slice(0, this.maxRecentSearches);
      
      this.recentSearches = searches;
      localStorage.setItem('recentSearches', JSON.stringify(searches));
    },
    applyRecentSearch(search) {
      this.searchQuery = search;
      this.handleSearch();
      this.showRecentSearches = false;
    },
    removeRecentSearch(index) {
      this.recentSearches.splice(index, 1);
      localStorage.setItem('recentSearches', JSON.stringify(this.recentSearches));
    },
    handleBlur() {
    // ÎìúÎ°≠Îã§Ïö¥Ïù¥ Î∞îÎ°ú ÏÇ¨ÎùºÏßÄÏßÄ ÏïäÎèÑÎ°ù ÏßÄÏó∞ ÏÑ§Ï†ï
      setTimeout(() => {
        this.showRecentSearches = false;
      }, 200);
    },

    applyFilters() {
      let result = [...this.movies];

      // Í≤ÄÏÉâÏñ¥ ÌïÑÌÑ∞ÎßÅ
      if (this.searchQuery) {
        result = result.filter((movie) =>
          movie.title.toLowerCase().includes(this.searchQuery.toLowerCase())
        );
      }

      // Ïû•Î•¥ ÌïÑÌÑ∞ÎßÅ
      if (this.selectedGenre) {
        result = result.filter((movie) =>
          movie.genre_ids.includes(Number(this.selectedGenre))
        );
      }

      // ÌèâÏ†ê ÌïÑÌÑ∞ÎßÅ
      if (this.minRating) {
        result = result.filter(
          (movie) => movie.vote_average >= Number(this.minRating)
        );
      }

      // Ï†ïÎ†¨
      if (this.sortBy) {
        result.sort((a, b) => {
          switch (this.sortBy) {
            case 'popularity':
              return b.popularity - a.popularity;
            case 'release_date':
              return new Date(b.release_date) - new Date(a.release_date);
            case 'vote_average':
              return b.vote_average - a.vote_average;
            default:
              return 0;
          }
        });
      }

      this.filteredMovies = result;
    },
    resetFilters() {
      this.searchQuery = '';
      this.selectedGenre = '';
      this.minRating = '';
      this.sortBy = '';
      this.filteredMovies = [...this.movies];
    },
    goToDetail(movieId) {
      this.$router.push(`/movie/${movieId}`);
    },
  },
  created() {
    this.fetchMovies();
    // Ï†ÄÏû•Îêú ÏµúÍ∑º Í≤ÄÏÉâÏñ¥ Î∂àÎü¨Ïò§Í∏∞
    const savedSearches = localStorage.getItem('recentSearches');
    if (savedSearches) {
      this.recentSearches = JSON.parse(savedSearches);
    }
  },
  mounted() {
    // ÎìúÎ°≠Îã§Ïö¥ Ïô∏Î∂Ä ÌÅ¥Î¶≠ Ïãú Îã´Í∏∞
    document.addEventListener('click', (e) => {
      if (!e.target.closest('.search-input-container')) {
        this.showRecentSearches = false;
      }
    });
  }
};
</script>

<style scoped>
