  background: rgba(255, 255, 255, 0.1);
}

.remove-search {
  background: none;
  border: none;
  color: #666;
  cursor: pointer;
  padding: 0 5px;
}

.remove-search:hover {
  color: #ff0000;
}

/* filters ÌÅ¥ÎûòÏä§Ïóê position: relative Ï∂îÍ∞Ä */
.filters {
  position: relative;
}

.search-input-container {
  position: relative;
  width: 200px;
}

.recent-searches-dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background: #141414;
  border: 1px solid #333;
